@import 'variables';

.table-container-wrapper {
   width: 1460px;
   max-width: 1460px;
   margin: 0 auto;

   @media (max-width:1460px) {
      width: 90%;
      max-width: 90%;
   }
}

.table {
   width: 100%;

   @media (max-width: 768px) {
      padding-right: 25px;
   }

   &-section {
      margin: -202px 0 90px;
      margin-left: -50px;

      @media (max-width: 992px) {
         margin: 0;
      }
   }

   &-heading {
      font-size: 14px;
      border-top: none !important;
      color: $dark-blue-color;
      font-weight: 400;
      text-align: left;
      padding-bottom: 30px;

      &:nth-of-type(3),
      &:nth-of-type(4),
      &:nth-of-type(5) {
         text-align: center;
      }

      &:last-of-type {
         text-align: right;
      }

      @media (max-width: 992px) {
         font-size: 12px;
      }
   }

   &-item {
      font-size: 14px;
      height: 50px;
      border-top: none !important;
      padding-bottom: 26px;
      color: $grey-color1;

      .text-darkblue {
         margin-left: 3px;
      }

      &:nth-of-type(3),
      &:nth-of-type(4),
      &:nth-of-type(5) {
         text-align: center;
      }

      &:last-of-type {
         text-align: right;
      }

      @media (max-width: 992px) {
         white-space: nowrap;
         padding-right: 35px;
         font-size: 12px;

         &:last-of-type {
            padding-right: 0;
         }
      }
   }

   tr:last-of-type .table-item {
      padding-bottom: 0;
   }

   &-container {
      background-color: var(--primary-color);
      border-radius: 12px;
      padding: 44px 24px;
      margin-top: 72px;
      width: 100%;
      background: #FFFFFF;
      box-shadow: 0px 20px 50px rgb(0 0 0 / 10%);
      border-radius: 8px;

      @media (max-width: 992px) {
         overflow: auto;
         padding: 30px 20px;
      }
   }

   &-above-text {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      margin-bottom: 20px;
      /* margin-top: -43px; */
      top: 2px;
      margin-left: 51px;
      position: relative;

      @media (max-width:1460px) {
         width: 100%;
         max-width: 100%;
         overflow: auto;
      }

      @media (max-width: 1280px) {
         justify-content: flex-start;
         overflow: auto;
      }
      @media (max-width:992px){
         margin-left: 0px;
      }

      img {
         margin-right: 25px;
      }

      a {
         text-decoration: none;
         white-space: nowrap;
         padding: 6px 0;
         display: flex;
         align-items: center;

         &:hover{
            color: #0052FF;
         }

         @media (max-width: 1280px) {
            white-space: nowrap;
            font-size: 12px;
         }

         +a {
            padding-left: 25px;
            margin-left: 25px;
            border-left: 1px solid $dark-blue-color;

            @media (max-width: 1280px) {
               padding-left: 10px;
               margin-left: 10px;
            }
         }
      }

      .hot-link {
         &:before {
            content: '';
            background-image: url(../img/link.svg);
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center;
            margin-right: 16px;
            width: 15px;
            height: 19px;
            display: inline-block;

            @media (max-width: 992px) {
               margin-right: 9px;
            }
         }
      }

      .more-link {

         &:after {
            content: '';
            background-image: url(../img/icons/darkblue-arrow-right.svg);
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center;
            margin-left: 19px;
            width: 7px;
            height: 12px;
            display: inline-block;
         }
      }
   }
}

.tabs-nav {
   margin-bottom: 40px;
   border-bottom: 1px solid #556176;
}

.b-tab {
   display: none;
}

.b-tab.active {
   display: block;
}

.b-nav-tab {
   transition: .7s;
   display: inline-block;
   padding: 0 25px 15px;
   text-decoration: none;
   font-size: 18px;
   font-weight: 500;
   color: $grey-color1;
   position: relative;

   &:hover{
      transition: .7s;
      color: #000000;
   }
}

.b-nav-tab.active {
   color: #000000;
   font-weight: 700;

   &:after {
      content: '';
      background-color: $blue-color;
      width: 100%;
      height: 3px;
      border-radius: 5px;
      display: inline-block;
      position: absolute;
      bottom: -2px;
      left: 0;
   }
}

.intro {
   &-swiper {
      width: 100%;
      top: 18px;

      @media (min-width:350px) {
         width: 100%;
         display: flex;
         justify-content: center;
         text-align: center;
      }

      @media (min-width:1060px) {
         width: 100%;
         display: flex;
         justify-content: center;
         text-align: start;
      }

      &-slide {
         width: 357px !important;
         margin-right: 29px !important;

         img {
            width: 346px;
            height: 204px;
            border-radius: 9px;

            @media (max-width:1535px) {
               width: auto;
               height: auto;
            }
         }
      }

      .intro-swiper-button-next,
      .intro-swiper-button-prev {
         outline: none;

         &:after {
            content: "";
            background-image: url(../img/icons/arrow-right.svg);
            background-size: contain;
            background-repeat: no-repeat;
            font-size: 0;
            width: 12px;
            height: 20px;

            @media (max-width: 768px) {
               width: 10px;
               height: 16px;
            }
         }
      }

      .intro-swiper-button-prev {
         &:after {
            transform: rotate(180deg);
         }
      }

      @media (max-width: 992px) {
         margin-top: 50px;
      }
   }
}